.subtotal{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    width: 300px;
    height: 150px;
    padding: 20px;
    background-color: #f3f3f3;
    border:1px solid #dddddd;
    border-radius: 3px;
     margin-left: 20px;
}
.subtotal__gift{
    display: flex;
    align-items: center;
}

.subtotal >button{
    background-color: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top:10px;
    border-color :#a88734 #9c7e31 #846a29;
    color:#111;
}
.chackout__right{
    margin-left: 200px;
}