.header{
    height: 60px;
    display: flex;
    align-items: center;
    background-color: black;
    position: sticky;
    top: 0;
    z-index: 100;
    color: white;
}
.header__logo{
    width: 100px;
    object-fit :contain;
    margin: 0 20px;
    margin-top: 18px;
}
.header__search{
    display: flex;
    flex:1;
    align-items: center;
    border-radius: 24px;
}
.header__searchInput{
    height: 12px;
    padding: 15px;
    border: none;
    width: 90%;
    border-radius: 2px;
}
.header__searchIcon{
    padding: 3px;
    height: 30px !important;
    color: black;
    background-color: #e29d43;
    border-radius: 2px;
    margin: 5px;
}

.header__optionLineOne{
    font-size: 10px;

}
.header__optionLineTwo{
    font-size: 13px;
    font-weight: 800;
}
.header__nav{
    display: flex;
    justify-content: space-evenly;
}
.header__option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: white;

}
.header__optionBasket{
    display: flex;
    align-items: center;
    color: white;

}
.header__basketCount{
    margin-left: 10px;
    margin-right: 10px;
}